  }
  
  
  /* SCROLL DOWN BUTTON */
  @-webkit-keyframes rev-ani-mouse {
      0% { opacity: 1;top: 29%;}
      15% {opacity: 1;top: 50%;}
      50% { opacity: 0;top: 50%;}
      100% { opacity: 0;top: 29%;}
  }
  @-moz-keyframes rev-ani-mouse {
      0% {opacity: 1;top: 29%;}
      15% {opacity: 1;top: 50%;}
      50% {opacity: 0;top: 50%;}
      100% {opacity: 0;top: 29%;}
  }
  @keyframes rev-ani-mouse {
      0% {opacity: 1;top: 29%;}
      15% {opacity: 1;top: 50%;}
      50% {opacity: 0;top: 50%;}
      100% {opacity: 0;top: 29%;}
  }
